.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

.formio-form {
  position: relative;
  min-height: 80px;
}

.formio-disabled-input .form-control.flatpickr-input {
  background-color: #eee;
}

.formio-component.has-error .invalid-feedback {
  display: block;
}

.formio-wysiwyg-editor {
  min-height: 200px;
  background-color: #fff;
}

.has-feedback .form-control {
  padding-right: 10px;
}

.has-feedback .form-control[type='hidden'] {
  padding-right: 0;
}

.has-error.bg-danger {
  padding: 4px;
}

.ql-source:after {
  content: '[source]';
  white-space: nowrap;
}

.quill-source-code {
  width: 100%;
  margin: 0;
  background: #1d1d1d;
  box-sizing: border-box;
  color: #ccc;
  font-size: 15px;
  outline: 0;
  padding: 20px;
  line-height: 24px;
  font-family: Consolas, Menlo, Monaco, 'Courier New', monospace;
  position: absolute;
  top: 0;
  bottom: 0;
  border: none;
  display: none;
}

.formio-component-tags tags {
  background-color: #fff;
}

.field-required:after {
  content: ' *';
  color: red;
}

.glyphicon-spin {
  -webkit-animation: formio-spin 1s infinite linear;
  animation: formio-spin 1s infinite linear;
}

@-webkit-keyframes formio-spin {
  from {
    -webkit-transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes formio-spin {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.button-icon-right {
  margin-left: 5px;
