.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

.formio-form {
  position: relative;
  min-height: 80px;
}

.formio-disabled-input .form-control.flatpickr-input {
  background-color: #eee;
}

.formio-component.has-error .invalid-feedback {
  display: block;
}

.formio-wysiwyg-editor {
  min-height: 200px;
  background-color: #fff;
}

.has-feedback .form-control {
  padding-right: 10px;
}

.has-feedback .form-control[type='hidden'] {
  padding-right: 0;
}

.has-error.bg-danger {
  padding: 4px;
}

.ql-source:after {
  content: '[source]';
  white-space: nowrap;
}

.quill-source-code {
  width: 100%;
  margin: 0;
  background: #1d1d1d;
  box-sizing: border-box;
  color: #ccc;
  font-size: 15px;
  outline: 0;
  padding: 20px;
  line-height: 24px;
  font-family: Consolas, Menlo, Monaco, 'Courier New', monospace;
  position: absolute;
  top: 0;
  bottom: 0;
  border: none;
  display: none;
}

.formio-component-tags tags {
  background-color: #fff;
}

.field-required:after {
  content: ' *';
  color: red;
}

.glyphicon-spin {
  -webkit-animation: formio-spin 1s infinite linear;
  -moz-animation: formio-spin 1s infinite linear;
  -o-animation: formio-spin 1s infinite linear;
  animation: formio-spin 1s infinite linear;
}

@-moz-keyframes formio-spin {
  from {
    -moz-transform: rotate(0);
  }
  to {
    -moz-transform: rotate(360deg);
  }
}

@-webkit-keyframes formio-spin {
  from {
    -webkit-transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes formio-spin {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}

.button-icon-right {
  margin-left: 5px;
}

.formio-component-submit .submit-success::after {
  font-family: 'Glyphicons Halflings';
  content: '\e013';
  position: relative;
  right: -4px;
  top: 1;
  line-height: 1;
}

.formio-component-submit .submit-fail::after {
  font-family: 'Glyphicons Halflings';
  content: '\e014';
  position: relative;
  right: -4px;
  top: 1;
  line-height: 1;
}

.formio-component-submit .submit-fail[disabled] {
  opacity: 1;
}

.form-control.flatpickr-input {
  background-color: #fff;
}

td > .form-group {
  margin-bottom: 0;
}

.signature-pad {
  position: relative;
}

.signature-pad-body {
  overflow: hidden;
}

.signature-pad-canvas {
  border-radius: 4px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.02) inset;
  border: 1px solid #f4f4f4;
}

.btn.signature-pad-refresh {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1000;
  padding: 3px;
  line-height: 0;
}

[dir='rtl'] .btn.signature-pad-refresh {
  left: unset;
  right: 0;
}

.choices__list--dropdown .choices__item--selectable {
  padding-right: 0;
}

.signature-pad-refresh img {
  height: 1.2em;
}

.signature-pad-footer {
  text-align: center;
  color: #c3c3c3;
}

.loader-wrapper {
  z-index: 1000;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.1);
}

.loader {
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -30px;
  margin-top: -30px;
  z-index: 10000;
  display: inline-block;
  border: 6px solid #f3f3f3;
  border-top: 6px solid #3498db;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}

.choices__list--dropdown {
  z-index: 100;
}

.choices__list--multiple .choices__item {
  border-radius: 0;
  padding: 2px 8px;
  line-height: 1em;
  margin-bottom: 6px;
}

.choices__list--single {
  padding: 0;
}

.choices__input {
  padding: 2px;
}

.choices[dir='rtl'] > * {
  text-align: right;
}

.choices[dir='rtl'] .choices__list--multiple .choices__item[data-deletable] {
  padding-left: 5px;
  float: right;
}

.choices[dir='rtl'] .choices__list--multiple .choices__item[data-deletable] .choices__button {
  float: left;
  margin: 0 8px 0 -4px;
  padding-left: unset;
  padding-right: 16px;
  border-left: unset;
  border-right: 1px solid #008fa1;
}

.formio-component-file .fileSelector {
  padding: 15px;
  border: 2px dashed #ddd;
  text-align: center;
}

.formio-component-file .fileSelector.fileDragOver {
  border-color: #127abe;
}

.formio-component-file .fileSelector .fa,
.formio-component-file .fileSelector .glyphicon {
  font-size: 20px;
  margin-right: 5px;
}

[dir='rtl'] .formio-component-file .fileSelector .fa,
[dir='rtl'] .formio-component-file .fileSelector .glyphicon {
  margin-right: unset;
  margin-left: 5px;
}

.formio-component-file .fileSelector .browse {
  cursor: pointer;
}

@-webkit-keyframes formio-dialog-fadeout {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes formio-dialog-fadeout {
  0% {
    opacity: 1;
